.main__listItem {
  list-style: none;
}
.main__code {
  font-size: 1.75rem;
  font-weight: 500;
}
.first {
  font-weight: 700;
}
.second {
  font-weight: 500;
}
.third {
  font-weight: 300;
}
.object {
  display: flex;
  flex-direction: column;
  justify-items: baseline;
  position: relative;
  padding-bottom: 65px;
}
.object__list {
  list-style: none;
  position: relative;
  padding-left: 10px;
}

.object__list-item {
  display: block;
  padding-left: 20px;
}

.object__triangle {
  left: -25px;
  top: 20px;
  position: absolute;
  height: 20px;
  width: 20px;
  background-image: url("../../images/triangle.png");
  background-repeat: no-repeat;
  background-size: cover;
  transition: all 0.5s ease-in-out;
  background-color: transparent;
  border: none;
}

.object__triangle-rotate {
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  transform: rotate(-90deg);
}
.object__list-cont {
  display: block;
  max-height: 500px;
  transition: max-height 0.5s ease-in-out;
  transform-origin: top;
  overflow: hidden;
}
.collapsed {
  max-height: 0px;
  transition: max-height 0.5s ease-in-out;
  transform-origin: top;
}
@media screen and (max-width: 1005px) {
  .object {
    padding-top: 25px;
    padding-bottom: 65px;
  }
}
