.project {
  display: grid;
  grid-template-columns: repeat(3, minmax(30%, 2fr));
  grid-gap: 20px;
  margin: 0 auto;
}
.projects__cont {
  margin: 0 auto;
}
@media screen and (max-width: 1200px) {
  .project {
    display: grid;
    grid-template-columns: repeat(2, minmax(40%, 2fr));
    grid-gap: 10px;
    margin: 0 auto;
  }
  .projects__cont {
    margin-bottom: 55px;
  }
}
@media screen and (max-width: 700px) {
  .project {
    display: grid;
    grid-template-columns: repeat(1, minmax(70%, 1fr));
    grid-gap: 10px;
    margin: 0 auto;
  }
}
