.header {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  background-color: darkslategray;
  color: white;
  font-size: 14px;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  margin: 0;
}
.header__title {
  font-size: 24px;
  margin-left: 20px;
}
.header-alt {
  opacity: 0.8 !important;
}

.header__theme-btn {
  width: 24px;
  height: 24px;
  border: none;
  background-color: transparent;
  margin: 0;
  margin-right: 10px;
}
.disable {
  opacity: 0.25;
}
.header__theme-cont {
  position: fixed;
  left: 25px;
  top: 120px;
}
.header__hamburger {
  height: 24px;
  width: 24px;
  background-image: url("../../images/hamburger.svg");
  border: none;
  background-color: transparent;
  background-position: center;
  background-size: cover;
}

.header__hamburger_hidden {
  display: none;
}
@media screen and (max-width: 1024px) {
  .header__logo {
    margin-left: 16px;
  }
}

@media screen and (max-width: 615px) {
  .header {
    padding-top: 16px;
    padding-bottom: 15px;
    flex-direction: column;
    align-items: center;
  }
  .header__title {
    margin-left: 0;
  }
  .header__theme-cont {
    margin-top: 155px;
    display: block;
    position: static;
  }
}
