.footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: darkslategray;
  padding: 35px;
  color: white;
  position: fixed;
  bottom: 0;
  left: 0;
}
.footer__links {
  padding-left: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 25%;
}
.footer__icons {
  transform: scale(1);
  transition: transform 0.5s ease-in-out;
}

.footer__icons:hover {
  transform: scale(1.25);
  transition: transform 0.5s ease-in-out;
}

@media screen and (max-width: 1224px) {
  .footer__links {
    width: 35%;
  }
}
@media screen and (max-width: 615px) {
  .footer {
    width: 100%;
    height: 18%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: darkslategray;
    padding: 25px;
    color: white;
    position: fixed;
    bottom: 0;
    left: 0;
  }
  .footer__links {
    margin: 0;
    padding: 0;
  }
}
